.App {
  height: 100%;
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-rows: auto 1fr;
  padding: 0 0 20px 0;
}
.header {
  height: 100px;
  color: orange;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header h1{
  text-shadow: -1px 2px 5px black;
}
.changePage {
  padding: 10px;
  display: flex;
  justify-content: start;
}
.changePageBtn {
  /*border: grey solid 1px;*/
  margin: 10px;
  min-height: 35px;
  min-width: 140px;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 1px 4px 0 grey;
}
.changePageBtn:hover {
  background-color: rgba(255, 169, 0, 0.29);
  /*color: white;*/
}
.changePage .changePageBtn.true {
  background-color: orange;
  font-weight: bold;
}
.changePage .changePageBtn.true:hover {
  color: black;
}
.content {
  /*border: grey solid 1px;*/
  margin: 0 15px;
  height: 100%;
  box-shadow: 1px 1px 4px 0 grey;
  display: grid;
  grid-template-rows: auto 1fr;
}
