.FileCheck {
    display: grid;
    grid-template-rows: auto 1fr;
}
.FileCheck_File {
    /*box-shadow: 1px 1px 4px 0 grey;*/
    display: grid;
    grid-template-columns: 1fr 200px 200px;
    margin: 0 20px;
    grid-gap: 20px;
    min-height: 30px;
}
.FileCheck_File input {
    width: 0;
    min-height: 30px;
}
.FileCheck_File label {
    width: 200px;
    cursor: pointer;
    box-shadow: 1px 1px 4px 0 grey;
    min-height: 30px;
    justify-self: center;
}
.FileCheck_File label:hover {
   background-color: #d4d4d4;
}
.FileCheck_addFilePath{
    justify-self: start;
    text-align: start;
    width: 100%;
    margin-right: 20px;
    box-shadow: 1px 1px 4px 0 grey;
    display: flex;
    align-items: center;
    padding: 0 10px;
    overflow: hidden;
    min-height: 30px;

}

.FileCheck_addFilePath span{
    font-weight: bold;
    margin-right: 15px;
}
.FileCheck_send {
    box-shadow: 1px 1px 4px 0 grey;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    min-height: 30px;
    width: 200px;
    justify-self: center;
}
.FileCheck_send.true:hover {
    background-color: #14b66b;
}
.FileCheck_send.true {
    background-color: #19e787;
    font-weight: bold;
}
.FileCheck_list {
    box-shadow: 1px 1px 4px 0 grey;
    margin: 20px;
    display: grid;
    grid-template-rows: auto 1fr auto;

}
.FileCheck_list_title {
    text-align: start;
    padding: 5px 10px;
    font-weight: bold;
    color: blue;
}
.FileCheck_list_main {

}
/* карточка */
.FileCheck_list_item {
    margin: 10px 20px;
    box-shadow: 1px 1px 4px 0 grey;
    border-radius: 10px;
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px 20px;
    cursor: pointer;
}
.FileCheck_list_item:hover {
    background-color: rgba(255, 169, 0, 0.29);
}
.FileCheck_list_date {
    font-size: 20px;
    font-weight: bold;
    text-align: start;
    line-height: 1;
}
.FileCheck_list_status {
    text-align: end;
    font-size: 20px;
    font-weight: bold;
    color: green;
}
.FileCheck_list_itemMain {
    grid-column: span 2;
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    flex-wrap: wrap;
    gap: 20px;
}
.FileCheck_list_all {

}
.FileCheck_list_high {

}
.FileCheck_list_low {

}
/* пагинация */
.FileCheck_list_pagination {
    padding: 0 0 0 20px;

}

@media (max-width: 991px)  {
    .FileCheck_File{
        grid-template-columns: 1fr 1fr;
    }
    .FileCheck_addFilePath{
        grid-column: span 2;
    }
}