

.Pagination {
    padding: 15px 20px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: grey;
    font-family: sans-serif;
    font-size: 90% !important;
}
.Pagination div {
    cursor: pointer;
}
.Pagination::selection {
     background-color: transparent;
 }

.pagesBox {
    margin-right: 1px;
    display: flex;
    border: orange solid 1px;
    border-right: none;
}
.pageNumber {
    min-width: 30px;
    height: 30px;
    /*padding: 5px 7px;*/
    border-right: orange solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.pageNumber:hover {
     background-color: rgba(53, 45, 67, 0.23);
 }


.pageNumber.active {
    font-weight: bold;
    color: black;
}

.ellipsisFirstBtn, .ellipsisLastBtn {
    padding: 5px;
    border-right: orange solid 1px;
}
.ellipsisFirstBtn:hover {
     background-color: rgba(53, 45, 67, 0.23);
 }
.ellipsisLastBtn:hover {
     background-color: rgba(53, 45, 67, 0.23);
 }

.firstPageBtn, .prevPageBtn,
.nextPageBtn, .lastPageBtn {
    width: 30px;
    margin-right: 1px;
    padding: 5px 7px;
    background-color: #ffa500;
    border: orange solid 1px;
    /*color: white;*/
    color: black;
}
.firstPageBtn:hover {
     border: transparent solid 1px;
     background-color: #d58b01;
 }
.prevPageBtn:hover {
     border: transparent solid 1px;
     background-color: #d58b01;
 }
.nextPageBtn:hover {
     border: transparent solid 1px;
     background-color: #d58b01;
 }
.lastPageBtn:hover {
     border: transparent solid 1px;
     background-color: #d58b01;
 }

.firstPageBtn.disabled, .prevPageBtn.disabled,
.nextPageBtn.disabled, .lastPageBtn.disabled {

}
.firstPageBtn.disabled:hover {
     background-color: orange;
 }
.prevPageBtn.disabled:hover {
     background-color: orange;
 }
.nextPageBtn.disabled:hover {
     background-color: orange;
 }
.lastPageBtn.disabled:hover {
     background-color: orange;
 }


.prevPageBtn, .nextPageBtn {
    padding: 5px 9px;
}

