.Table {
    position: fixed;
    z-index: 1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.82);
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(3px);
}
.Table .content {
    position: relative;
    margin: 5vh 5vw;
    max-height: calc(100vh - 10vh);
    height: auto;
    max-width: 700px;
    width: 100%;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto;
    overflow-y: auto;
    overflow-x: hidden;
    min-height: 400px;
}
.Table_list {

}
.close {
    position: absolute;
    z-index: 1;
    right: 10px;
    top: 10px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.close:before {
     content: '';
     position: absolute;
     top: 15px;
     left: 0;
     width: 30px;
     height: 2px;
     background-color: black;
     transform: rotate(-45deg);
 }

.close:after {
     content: '';
     position: absolute;
     top: 15px;
     left: 0;
     width: 30px;
     height: 2px;
     background-color: black;
     transform: rotate(45deg);
 }

.anamnes {
    position: absolute;
    left: -100%;
    background-color: white;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 50px;
    transition: left 0.3s linear 0s;
}
.anamnes.active {
    left: 0;
}
.anamnesHidden {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 213, 0, 0.15);
    background-color: #fae2c0;
    cursor: pointer;
}
.anamnesHidden svg {
    width: 40px;
    height: 40px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.2s linear 0.3s;
    opacity: 0.2;
}
.anamnesHidden:hover svg {
    opacity: 1;
}

.anamnes.active .anamnesHidden svg{
    transform: rotate(180deg);
}
.anamnesMain {
    text-align: start;
    padding: 15px;
}

.Table_list_title {
    margin-top: 10px;
    font-size: 20px;
    font-weight: bold;
}
.Table_filter {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
}
.Table_filter div{
    padding: 3px 10px;
    border: black solid 1px;
    border-radius: 5px;
    cursor: pointer;
}
.Table_filter div:hover{
    background-color: rgba(255, 169, 0, 0.29);
}
.Table_filter div.true{
    background-color: #ffa500;
}
.Table_list_head {
    height: 40px;
    margin: 10px 10px 0;
    display: grid;
    grid-template-columns: 50px 100px 1fr 150px;
    border: black solid 1px;
    overflow: hidden;
}
.Table_list_head div{
    height: 100%;
    border-right: black solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
}
.Table_list_head div:last-child{
    border-right: none;
}

/*item*/
.Table_list .item{
    overflow: hidden;
    margin: 0 10px;
    border: black solid 1px;
    border-top: none;
    display: grid;
    grid-template-columns: 50px 100px 1fr 150px;
}
.Table_list .item:last-child{
    margin-bottom: 20px;
}
.Table_list .item div{
    border-right: black solid 1px;
    padding: 5px;
}
.Table_list .item div:last-child{
    border-right: none;
}
.Table_list .item .anamnesBtn{
    background-color: white;
    cursor: pointer;
}
.Table_list .item .anamnesBtn:hover{
    background-color: #d9d9d9;
    text-decoration: underline;
}

